@import '../../variables.scss';

.skills {
    margin-top: 122px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    &_name {
        font-size: 32px;
        font-weight: 500;
        color: $text-neutral;
        span {
            color: $text-bold;
        }
    }
    &_divider {
        margin-left: 10px;
        width: 600px;
        height: 2px;
        background-color: $text-bold;
    }
    &_list {
        margin-top: 50px;
        display: grid;
        grid-template-columns: repeat(4, 270px);
        grid-template-rows: repeat(2, 290px);
        column-gap: 40px;
        row-gap: 20px;
    }
    &_item {
        padding: 30px;
        border: solid 1px $text-regular;
        color: $text-neutral;
        font-size: 12px;
        font-weight: 100;
        &__icon {
            height: 65px;
        }
        &__title {
            margin-top: 20px;
            font-size: 20px;
            font-weight: 500;
            color: $text-bold;
        }
        &__desc {
            margin-top: 20px;
        }
    }
}

@media (max-width: 576px) {
    .skills {
        &_list {
            grid-template-columns: 270px;
            margin: 50px auto 0 auto;
        }
        &_divider {
            width: 155px;
        }
    }
}

@media (min-width: 576px) and (max-width: 768px) {
    .skills {
        &_list {
            grid-template-columns: 300px;
            margin: 50px auto 0 auto;
        }
        &_divider {
            width: 190px;
        }
    }
}

@media (min-width: 768px) and (max-width: 992px) {
    .skills {
        &_list {
            grid-template-columns: repeat(2, 320px);
            grid-template-rows: repeat(4, 290px);
            row-gap: 40px;
            column-gap: 80px;
        }
        &_divider {
            width: 360px;
        }
    }
}

@media (min-width: 992px) and (max-width: 1200px) {
    .skills {
        &_list {
            grid-template-columns: repeat(4, 225px);
            grid-template-rows: repeat(2, 320px);
            column-gap: 20px;
        }
    }
}

@media (min-width: 1200px) and (max-width: 1400px) {
    .skills {
        &_list {
            grid-template-columns: repeat(4, 260px);
            column-gap: 33.7px;
        }
    }
}

@media (min-width: 1400px) {
    .skills {
        &_list {
            grid-template-columns: repeat(4, 300px);
            grid-template-rows: repeat(2, 300px);
        }
    }
}